import UserTable from './UserTable';
import { getUsers } from '../../../lib/db/mysql-primary'; // adjust path to your data layer

export default async function UsersPage() {
  const users = await getUsers(); // Fetch from Prisma or your backend

  return (
    <div className="p-6">
      <h2 className="text-2xl font-bold text-slate-800 mb-2">Users</h2>
      <p className="text-slate-600 mb-6">Manage system users</p>

      <div className="bg-white rounded-lg shadow p-6">
        <UserTable users={users} />
      </div>
    </div>
  );
}
